import React from 'react'

import {Layout, Mdx, Link  } from "actb"
import {List, Avatar} from "antd"
import {distinctColors} from 'actl'

import SnippetsIntro from "fragments/snippets.md"

const Panel = Layout.Panel
const collections = distinctColors.distinctBackgroundColors([
    {
        keyword: "Gatsby",
        slug:"/snippets/main/static-intro/",
        title: "Gatsby and ACT blog starter",
        description: `Gatsby is a static site generator, coming with a big ecosystem of tools to build efficient, SEO optimized sites. 
        This blog itself is built using Gatsby and ACT Gatsby starter. Snippets contain some useful extracts of configuration, GraphQL and information about files and directory structure`
    },
    {
      keyword: "AWS",
      slug:"/snippets/serverless/aws-intro/",
      title: "AWS and Serverless",
      description: `AWS is a popular cloud provider. Built around idea of serverless architecture, it may help significantly reduce the cost of infrastructure ownership. This collection of snippets 
      should help to accomplish common simple tasks, when working with service`
    },      
])

function ItemAvatar({keyword, backgroundColor}){
    return <Avatar size="large" style={{backgroundColor}}>{ keyword }</Avatar>
}

export default function SnippetsPage({pageContext:context}){
  const props = Object.assign({}, {layout:context.layout, 
        title: "Snippets. Short code extracts. Useful short side notes "
    }, context.frontmatter)

  return (
    <Layout {...props}>
      <Panel className="snippets-page__content">
        <Mdx>
            <SnippetsIntro/>
        </Mdx>
        <List
            itemLayout="horizontal"
            dataSource={collections}
            renderItem={item => (
            <List.Item>
                <List.Item.Meta
                avatar={<ItemAvatar {...item}/>}
                title={<Link to={item.slug}>{item.title}</Link>}
                description={ item.description }
                />
            </List.Item>
            )}
        />             
      </Panel>
    </Layout>
  )
}
